.layout{
  position :absolute;
  top :0;
  bottom: 0;
  left :0;
  width : 100%;
  height : 100%;
}

.navbar {
  align-items: flex-start;
}
.navbar .text-light{
  text-align: left;
  padding-top : 10px;
  color :#f8f9fa9e !important;
}
.menu-toggler{
  position: relative;
  width : 50px;
  height :50px;
  cursor: pointer;
  box-sizing: border-box;
  z-index: 1;
}
.menu-toggler span{
  position: absolute;
  width : 25px;
  height: 1px;
  background: #fff;
  top :calc(50% - 1px);
  left :calc(50% - 15px);
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.menu-toggler span:nth-child(1){
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}
.menu-toggler.active span:nth-child(1){
  height: 2px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.menu-toggler.active span:nth-child(2){
  height: 2px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
body{
  margin: 0;
}
.App {
  text-align: center;
  height: 100vh;
    overflow: hidden;
}
.background_video{
  position: relative;
  width: 100%;

}
.background_video video{
