}


.app-loader{
	position :absolute;
  top :0;
  bottom: 0;
  left :0;
  width : 100%;
	height : 100%;
	background-color: #fff;
	display: flex;
  justify-content: center;
  align-items: center;
}
.text-container h1{
	padding: 0;
	color: #000000;
}
.text-container h1 span:before {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    font-size: 15rem;
    font-weight: 900;
    text-align: center;
    content: 'HTML';
    text-transform: uppercase;
    -webkit-animation: animate infinite 4s;
            animation: animate infinite 4s;
}


@-webkit-keyframes animate
{
	0%{
		content: 'Bold'
	}
	10%{
		content: 'Loud'
	}
	20%{
		content: 'Sick'
	}
	30%{
		content: 'Brave'
	}
	40%{
		content: 'Lite'
	}
	50%{
		content: 'Bold'
	}
	60%{
		content: 'Loud'
	}
	70%{
		content: 'Sick'
	}
	80%{
		content: 'Brave '
	}

	90%{
		content: 'Oops'
	}
}


